
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }

  @media (max-width: 768px) {
    .app-container {
      width: 100%;
      overflow-x: hidden;
    }
    
    .sidebar {
      width: 85%;
      max-width: 280px;
    }
    
    /* Add an overlay when sidebar is active */
    .sidebar.translate-x-0::before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: -1;
    }
  }
  

